.app__testimonial {
	flex: 1;
	width: 100%;
	flex-direction: column;
}

.app__testimonial-item {
	width: 60%;
	min-height: 320px;
	background-color: var(--white-color);
	display: flex;
	padding: 2rem;
	border-radius: 15px;
	box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
	transition: all 0.3s ease-in-out;
}
.app__testimonial-item img {
	width: 100px;
	height: 100px;
	border-radius: 50%;
	object-fit: cover;
}
@media screen and (min-width:2000px) {
	.app__testimonial-item {
		min-height: 450px;
	}
}
@media screen and (min-width:2000px) {
	.app__testimonial-item img {
		height: 150px;
		width: 150px;
	}
}
@media screen and (max-width:850px) {
	.app__testimonial-item {
		width: 100%;
	}
}
@media screen and (max-width:600px) {
	.app__testimonial-item {
		flex-direction: column;
	}
}

.app__testimonial-content {
	flex: 1;
	height: 100%;
	padding: 0 2rem;
	text-align: left;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: flex-start;
}
.app__testimonial-content p {
	font-size: 1.25rem;
	line-height: 2rem;
	color: var(--black-color);
	font-family: var(--font-base);
}
.app__testimonial-content h4 {
	font-weight: 600;
	color: var(--secondary-color);
	margin-top: 2rem;
}
.app__testimonial-content h5 {
	font-weight: 400;
	color: var(--gray-color);
	margin-top: 5px;
}

.app__testimonial-btns {
	flex-direction: row;
	margin-top: 1rem;
}
.app__testimonial-btns div {
	width: 50px;
	height: 50px;
	border-radius: 50%;
	background-color: var(--white-color);
	margin: 1rem;
	transition: all 0.3s ease-in-out;
}
.app__testimonial-btns div svg {
	width: 20px;
	height: 20px;
	color: var(--secondary-color);
}
.app__testimonial-btns div:hover {
	background-color: var(--secondary-color);
}
.app__testimonial-btns div svg:hover {
	color: var(--white-color);
}



.app__testimonial-brands {
	width: 80%;
	flex-wrap: wrap;
	margin-top: 2rem;
}
.app__testimonial-brands div {
	width: 150px;
	margin: 1.5rem;
}
.app__testimonial-brands div img {
	width: 100%;
	height: auto;
	object-fit: cover;
	filter: grayscale(1);
}
.app__testimonial-brands div img:hover {
	filter: grayscale(0);
}
@media screen and (min-width: 2000px) {
	.app__testimonial-brands div {
		width: 210px;
		margin: 2rem;
	}
}
@media screen and (max-width: 450px) {
	.app__testimonial-brands div {
		width: 120px;
		margin: 1rem;
	}
}
@media screen and (max-width: 800px) {
	.app__testimonial-brands {
		width: 100%;
	}
}